.Header {
    margin: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    height: 80px;
    padding-top: 20px;
    width: 80%;
    max-width: 1200px;

}
.Header .menu {
    display: inline-block;
    margin: 4px;
    vertical-align: top;
    line-height: 2.5em;
}
.Header ul.menu li {
    display: inline-block;
    margin-left: 10px;
}
.Header .logo {
    display: inline-block;
    text-align: left;
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.Header .main-menu {
    -ms-flex-positive: 3;
        flex-grow: 3;
}
.Header .login-menu {
    -ms-flex-positive: 1;
        flex-grow: 1;
}
.Header .bars {
    display: none;
    vertical-align: top;
    line-height: 2.5em;
    -ms-flex-positive: 0;
        flex-grow: 0;
}

@media screen and (max-width: 800px) {
    .Header .main-menu, .Header .login-menu {
        display: none;    
    } 
    .Header .bars {
        display: inline-block;
    }
    
}



